<template>
  <dashboard-barber-card>
    <v-row>
      <v-col v-if="barber" class="py-0" cols="12" sm="12" md="5">
        <h1 class="titulo">
          Barbeiro:
        </h1>
        <div class="image-container d-flex align-center mt-2">
          <div class="image" :style="{ backgroundImage: `url(${image})` }"></div>
          <span class="nome text-color ml-4">{{ barberComputed.name }}</span>
        </div>
      </v-col>
      <v-col cols="12" sm="12" md="7" class="py-0 d-flex flex-column mt-6 mt-md-0"
      :class="!barber ? 'aligin-md-start' : 'align-md-end'">
        <h1 v-if="!barber" class="titulo text-left d-block pl-2">
          Escolha o barbeiro:
        </h1>
        <select-field 
          :label="null"
          placeholder="Escolha o barbeiro"
          :outlined="true"
          :dense="true"
          class="input"
          :items="barberItems"
          :value="barber"
          @onChange="changeBarber"
        />
        <div v-if="barber" class="atendimentos text-color text-md-center mt-4 d-md-flex align-md-center">
          <span class="d-block d-md-inline">Atendimentos do período:</span> <span class="number-schedules success ml-md-4 mt-2 mt-md-0 d-inline-block d-md-inline">{{ schedules }} agendamentos</span>
        </div>
      </v-col>
    </v-row>
  </dashboard-barber-card>
</template>

<script>
import DashboardBarberCard from './DashboardBarberCard'
import SelectField from '@/components/globais/form/select-field/SelectField'
export default {
  name: 'DashboardChooseBarber',
  components: { DashboardBarberCard, SelectField },
  props: {
    schedules: { default: 0 }
  },
  data: () => ({
    barber: null,
    barbers: []
  }),
  computed: {
    image () {
      return `${this.$url_backend}${this.barberComputed.image_url}`
    },
    barberComputed () {
      return this.barbers.find(b => Number(this.barber) === Number(b.id))
    },
    barberItems () {
      return this.barbers.map(barber => {
        return {
          value: barber.id,
          text: barber.name
        }
      })
    }
  },
  mounted () {
    this.getBarbers()
  },
  methods: {
    changeBarber (value) {
      this.barber = value
      this.$emit('changeBarber', value)
    },
    getBarbers () {
      const errorMessage = 'Não foi possível cadastrar a barbearia!'
      this.$axios.get(`/barber/by-barbershop`)
      .then(response => {
        if (response.status === 200) {
          if (Number(response.data.codigo) === 200) {
            this.barbers = response.data.corpo
          } else this.setError(response.data.corpo)
        } else this.setError(response.data.corpo)
      }).catch (() => this.setError(errorMessage))
    }
  }
}
</script>

<style scoped>
.titulo { color: #919191; font-size: 1rem; width: 100%; }
.text-color { color: #4F4F4F; font-weight: 500; }
.input { border-radius: 45px; width: 220px; float: right; }
.number-schedules { border-radius: 45px; padding: 10px 30px; }
.image {
  display: block;
  width: 70px;
  height: 70px;
  background: #ccc;
  border-radius: 50%;
  background-size: contain;
  overflow: hidden;
}
</style>